<<template>
  <div>
    <div class="col-sm-12">
      <div class="col-sm-4 center login_box text_center">
        <h1 class="main_heading">Hello</h1>
        <p>Please Sign in so we can get started.</p>
        <form class="text_left" v-on:submit.prevent="fetchdata">
          <div class="form-group">
            <i class="fa fa-envelope form_icon" aria-hidden="true"></i>
            <input type="email" class="form-control text_box" v-model="username" id="displayName"
                   placeholder="Username">
          </div>
          <div class="form-group">
            <i class="fa fa-key form_icon" aria-hidden="true"></i>
            <input type="password" class="form-control text_box" v-model="password" id="roomNumber"
                   placeholder="Password">
          </div>
          <div class="checkbox">
            <label>
              <input type="checkbox" value="">
              <span class="cr"><i class="cr-icon fa fa-check"></i></span>
              Remember Me
            </label>
          </div>
          <div class="form-group">
            <button type="submit"  class="btn btn-default btn-block main_button">
              Submit
            </button>
          </div>
          <div class="form-group text_center">
            <button type="button" class="btn btn-link btn_link" data-toggle="modal"
                    data-target="#forgot_password">Forgot Password?
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import db from '@/components/js/fireBaseInit'

export default {
  name: 'loginForm',
  data () {
    return {
      username: '',
      password: '',
      admin_id: '',
      date: new Date().toDateString().substr(4)
    }
  },
  methods: {
    fetchdata () {
      db.collection('Admins').where(('email_id', '==', this.username) && 'password', '==', this.password).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          db.collection('ActiveAdmins').doc(this.date).collection('AdminList').get().then(querySnapshot => {
            if (querySnapshot.size === 0) {
              this.$router.push({name: 'adminSchedule', params: {admin_id: doc.data().admin_id}})
            }
            else {
              querySnapshot.forEach(docu => {
                if (parseInt(docu.id) === parseInt(doc.data().admin_id)) {
                  this.$router.push({name: 'adminMeeting', params: {admin_id: doc.data().admin_id}})
                } else {
                  this.$router.push({name: 'adminSchedule', params: {admin_id: doc.data().admin_id}})
                }
              })
            }
          })
        })
      })
    }
  }

}
</script>

<style scoped>

</style>



