<template>
    <div class="container-fluid full_height client">
        <div class="row full_height">
            <div class="col-sm-8 side_left flex full_height">
                <div class="col-sm-12 center login_box text_center">
                   <image_logo_b></image_logo_b>
                    <h1 class="main_heading">Already registered for the Queue?</h1>
                    <p class="sub_head">Please enter the token number to re-join the queue.</p>
                    <br>
                    <rejoin_form></rejoin_form>
                </div>
            </div>

            <div class="col-sm-4 side_right full_height register">
                <div class="row content_right">
                    <user_account></user_account>
                    <div class="col-sm-12 q_heading">
                        Your current position
                    </div>
                    <q_container></q_container>
                    <total_number></total_number>
                </div>
                <client_footer></client_footer>
            </div>
        </div>
    </div>
</template>

<script>
import rejoin_form from '@/components/pages/user/rejoin_form'
import client_footer from '@/components/shared/client_footer'
import image_logo_b from '@/components/shared/image_logo_b'
import q_container from '@/components/shared/q_container'
import total_number from '@/components/shared/total_number'
import user_account from '@/components/shared/user_account'
export default {
  name: 're-join',
  components: {
    total_number,
    image_logo_b,
    client_footer,
    q_container,
    rejoin_form,
    user_account
  }
}
</script>

<style scoped>

</style>



