<template>
    <div class="form-group col-md-10 col-md-offset-1 col-sm-8 col-sm-offset-2">
        <div class="total_q">
            Total number in queue
            <div class="number" >{{ totalNumber}}</div>
        </div>
    </div>
</template>

<script>
import db from '@/components/js/fireBaseInit'
export default {
  name: 'total_number',
  data () {
    return {
      totalNumber: 0,
      date: new Date().toDateString().substr(4)
    }
  },
  mounted () {
    db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id).collection('UserList').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        this.totalNumber++
      })
    })
  }
}
</script>

<style scoped>

</style>



