<template>
    <div class="col-sm-12 center login_box text_center">
        <form class="text_left" v-on:submit="cancelMeeting">
            <div class="row">
                <div class="form-group col-sm-4 col-sm-offset-4">
                    <button type="submit" class="btn btn-default btn-block cancel_button">Cancel Meeting</button>
                </div>
            </div>
            <div class="row">
                <div class="form-group col-sm-12">
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/components/js/fireBaseInit'

export default {
  name: 'adminCancel',
  data () {
    return {
      date: new Date().toDateString().substr(4)
    }
  },
  methods: {
    cancelMeeting () {
      console.log(this.$route.params.admin_id)
      if (confirm('Do you want to cancel meeting?')) {
        // db.collection('ActiveAdmins').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id.toString()).delete().then(doc => {
        //   db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id.toString()).collection('UserList').delete().then(docRef => {
        //     console.log(docRef)
        //     alert('Your meeting cancelled')
        //     console.log('meeting cancelled')
        //     // this.$router.push('/')
        //   }).catch(err => {
        //     console.log(err)
        //   })
        // })
        db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id.toString()).collection('UserList').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            console.log(doc.data())
            doc.data().delete().then(docRef => {
              console.log('meeting canceled')
            })
          })
        })
      }
    }
  }
}
</script>

<style scoped>

</style>



