<template>
  <div>
    <div class="col-sm-12 no_padding">
      <!-- ------------- User Card -------------- -->
      <div class="user_card text_left">
        <div class="user_pic flex">
          <div class="pic"><i class="fa fa-user" aria-hidden="true"></i></div>
        </div>
        <div class="user_box">
          <div class="username">
            <span>Username</span>
            <i class="fa fa-caret-down" aria-hidden="true"></i>
          </div>
        </div>
        <!-- ------------- Dropdown Box -------------- -->
        <div class="dropdown">
          <ul>
            <li data-toggle="modal" data-target="#profile_view"><i class="fa fa-user" aria-hidden="true"></i> My Profile
            </li>
            <li data-toggle="modal" data-target="#settings_view"><i class="fa fa-cog" aria-hidden="true"></i> Settings
            </li>
            <li><i class="fa fa-power-off" aria-hidden="true"></i> Sign Out</li>
          </ul>
        </div>
      </div>
    </div>

    <div class="modal fade " id="profile_view" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-lg profile_view" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <div class="row">
              <h1 class="main_heading">My Profile</h1><br>
              <div class="col-sm-4 text_center">
                <div class="profile_pic">
                  <i class="fa fa-user" aria-hidden="true"></i>
                </div>
              </div>
              <div class="col-sm-8">
                <table class="table profile_table">
                  <tbody>
                  <tr>
                    <td width="150px">Name</td>
                    <td>:</td>
                    <td> {{ admin[0].first_name }}</td>
                  </tr>
                  <tr>
                    <td>Email</td>
                    <td>:</td>
                    <td> {{ admin[0].email_id }}</td>
                  </tr>
                  <tr>
                    <td>Phone</td>
                    <td>:</td>
                    <td> {{ admin[0].phone_number }}</td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <router-link v-bind:to="{name: 'editAdmin', params: {admin_id : admin_id}}" class="btn-floating btn-large yellow">
            <button type="button" class="btn btn-default main_button edit_button pull-left">Edit Profile
            </button>
            </router-link>
            <button type="button" class="btn btn-default main_button" data-dismiss="modal">Close</button>
          </div>
        </div>
        <!-- /.modal-content -->
      </div>
      <!-- /.modal-dialog -->
    </div>

    <div class="modal fade " id="settings_view" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-lg profile_view" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <div class="row">
              <h1 class="main_heading">My Profile</h1><br>
              <div class="col-sm-4 text_center">
                <div class="profile_pic">
                  <i class="fa fa-user" aria-hidden="true"></i>
                </div>
              </div>
              <div class="col-sm-8">
                <h1>hiiii</h1>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default main_button" data-dismiss="modal">Close</button>
          </div>
        </div>
        <!-- /.modal-content -->
      </div>
      <!-- /.modal-dialog -->
    </div>
  </div>
</template>

<script>
import db from '../js/fireBaseInit'

export default {
  name: 'userDropdown',
  data () {
    return {
      admin: [],
      admin_id: this.$route.params.admin_id
    }
  },
  mounted () {
    console.log('hh')
    db.collection('Admins').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        if (parseInt(doc.id) === parseInt(this.$route.params.admin_id)) {
          console.log(doc.data())
          const data = {
            first_name: doc.data().first_name,
            last_name: doc.data().last_name,
            email_id: doc.data().email_id,
            phone_number: doc.data().phone_number
          }
          this.admin.push(data)
          console.log(data, this.admin)
        }
      })
    })
  }
}
</script>

<style scoped>

</style>



