<template>
  <div class="col-sm-12 center  text_center">
    <form class="text_left" v-on:submit.prevent="meeting">
      <div class="row">
        <div class="form-group col-sm-6">
          <!--<i class="fa fa-clock form_icon" aria-hidden="true"></i>-->
          <timeselector placeholder="Start-time" v-model="start_time"></timeselector>
          <!--<input type="text" class="form-control text_box time_picker show_time" v-model="start_time" placeholder="Start Time">-->
        </div>
        <div class="form-group col-sm-6">
          <!--<i class="fa fa-clock form_icon"  aria-hidden="true"></i>-->
          <timeselector placeholder="End-time" v-model="end_time"></timeselector>
          <!--<input type="text" class="form-control text_box time_picker show_time" v-model="end_time" placeholder="End Time">-->
        </div>
      </div>
      <div class="row">
        <div class="form-group col-sm-6">
          <i class="fa fa-user form_icon" aria-hidden="true"></i>
          <input type="text" class="form-control text_box" id="" v-model="no_meeting" placeholder="No of meetings">
        </div>
        <div class="form-group col-sm-6">
          <div class="checkbox">
            <label>
              <input type="checkbox" @click="eventcheck($event)">
              <span class="cr"><i class="cr-icon fa fa-check"></i></span>
              Repeat Schedule
            </label>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="form-group col-sm-6">

        </div>
        <div class="form-group col-sm-6">
          <button type="submit" class="btn btn-default btn-block main_button">Schedule</button>
        </div>
      </div>
      <div class="row">
        <div class="form-group col-sm-12">

        </div>
      </div>
    </form>
  </div>
</template>

<script>
import db from '@/components/js/fireBaseInit'
import Timeselector from 'vue-timeselector'
export default {
  name: 'sheduleForm',
  components: {
    Timeselector
  },
  data () {
    return {
      start_time: '',
      end_time: '',
      no_meeting: '',
      date: new Date().toDateString().substr(4),
      repeat: ''
    }
  },
  methods: {
    eventcheck: function (event) {
      if (event.target.checked) {
        console.log('checked')
        this.repeat = true
      }
      else {
        console.log('not checked')
        this.repeat = false
      }
    },
    meeting () {
      console.log(typeof this.$route.params.admin_id)
      db.collection('ActiveAdmins').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id.toString()).set({
        // start_time: this.start_time.toString().substr(17, 8),
        start_time: this.start_time.toString(),
        // end_time: this.end_time.toString().substr(17, 8),
        end_time: this.end_time.toString(),
        no_meeting: this.no_meeting,
        repeat: this.repeat
      }).then(docRef => {
        console.log('Admin Scheduled')
      }).catch(err => {
        console.log(err)
      })
      this.$router.push({name: 'adminMeeting'})
    }
  }
}

</script>

<style scoped>
  </style>



