<template>
<div>
  <img src="@/assets/img/logo_b.png" class="brand_logo"/>
  <h1 class="main_heading">You have Meetings Scheduled at {{ startTime}}</h1>
  <p class="sub_head">You can join the meeting room by clicking the "Join Room" button</p>
</div>
</template>

<script>
import db from '@/components/js/fireBaseInit'
export default {
  name: 'meetingInfo',
  data () {
    return {
      startTime: null,
      date: new Date().toDateString().substr(4)
    }
  },
  created: function () {
    db.collection('ActiveAdmins').doc(this.date).collection('AdminList').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        if (parseInt(doc.id) === parseInt(this.$route.params.admin_id)) {
          this.startTime = doc.data().start_time
          console.log(doc.data().start_time)
        }
      })
    })
  }
}
</script>

<style scoped>

</style>



