<template>
  <div>
    <!-- ---------------- Q List 1 ---------------- -->
    <div class="col-sm-12 q_list_box text_left" v-for="(user,i) in users" v-bind:key="user.username"
         @click="details = !details; selectedIndex = i;">
        <div class="q_order_number flex">
          <div class="token"> {{ i+1 }}</div>
        </div>

        <div class="user_box">
          <div class="username">{{ user.firstname}} {{ user.last_name}}</div>
          <div class="email">{{ user.email_id}}</div>
        </div>
        <div class="call_box flex">
          <div class="call_btn"><i class="fa fa-phone" aria-hidden="true"></i></div>
        </div>
      <div class="col-sm-12 no_padding user_details" v-show="details && selectedIndex === i" @click="!details">
        <table class="table user_details_table">
          <tbody>
          <tr>
            <td width="100px">Name</td>
            <td>:</td>
            <td>{{ user.firstname}} {{ user.last_name}}</td>
          </tr>
          <tr>
            <td width="100px">Email</td>
            <td>:</td>
            <td>{{ user.email_id}}</td>
          </tr>
          <tr>
            <td width="100px">ID Proof</td>
            <td>:</td>
            <td>ADR12015356656</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import db from '../js/fireBaseInit'

export default {
  name: 'awaitingQueue',
  data () {
    return {
      username: '',
      firstname: '',
      lastname: '',
      users: [],
      details: true,
      date: new Date().toDateString().substr(4),
      selectedIndex: ''
    }
  },
  // methods: {
  //   fetchdata () {
  created () {
    db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id).collection('UserList').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'firstname': doc.data().first_name,
          'last_name': doc.data().last_name,
          'email_id': doc.data().email_id

        }
        this.users.push(data)
      })
    })
  }
  //   }
  // }

}
</script>

<style scoped>

</style>



