<template>
    <div class="col-sm-12">
        <div id="q_container">
            <div id="main">
                <div id="content">{{qPosition}}</div>
                <div id="outer-circle">
                    <div id="inner-circle">
                        <div id="center-circle"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import db from '@/components/js/fireBaseInit'
    export default {
        name: 'q_container',
        data () {
            return {
                qPosition: 'Q',
                date: new Date().toDateString().substr(4)

            }
        },
        created () {
            db.collection('ActiveUsers').doc(this.date).collection('AdminList').doc(this.$route.params.admin_id).collection('UserList').orderBy('time').get().then((querySnapshot) => {
                this.qPosition = querySnapshot.docs.findIndex(item => item.data().user_id === this.$route.params.user_id) + 1
            })
        }
    }
</script>

<style scoped>

</style>




